import React from "react";
import Clock from "./clock";

function Life(){
    return(
<div className="life">
    <h1> </h1>
    <div className="componentscode">
        <pre>
            <code>
            class Clock extends React.Component {"{"} <br/>
  constructor(props) {"{"} <br/>
    super(props);<br/>
    this.state ={'{date: new Date()}'};<br/>
  {'}'}<br/>

  componentDidMount() {"{"}<br/>
    this.timerID = setInterval(<br/>
     {' () => this.tick(),'}<br/>
      1000<br/>
    );<br/>
  {'}'}<br/>

  componentWillUnmount() {"{"} <br/>
    clearInterval(this.timerID);<br/>
  {'}'}<br/>

  tick() {"{"}<br/>
    this.setState({"{"}<br/>
     {' date: new Date()'}<br/>
   {'}'}); <br/>
  {'}'}<br/>

  render() {"{"} <br/>
    return ( <br/>
     {' <div>'} <br/>
        {'<h1>, !</h1>'}<br/>
        {'<h2> {this.state.date.toLocaleTimeString()}.</h2>'}<br/>
      {'</div>'}<br/>
    ); <br/>
  {'}'}<br/>
{'}'}<br/>
</code>
        </pre>
    </div>
    <Clock/>
 <ol>
<li>   {'<Clock />'}  ReactDOM.render(), React   . Clock    ,
      this.state    .</li>
 <li>React   render()  Clock.   React ,    .
       React  DOM ,      Clock.</li>
 <li>    Clock   DOM, React     componentDidMount().
         Clock    ,    tick()   .</li> 
 <li>  tick() .  tick()   React   ,  setState()  
      . React        render().    this.state.date  
      render()   ,  React  DOM.    Clock    UI.</li>
  <li>  Clock -   DOM, React     componentWillUnmount()
         .</li>             
 </ol>
 
</div>
    )
}

export default Life