import React from "react";
import { Input } from 'antd';
const { Search } = Input;


function Form(){
  return(
      <div className="form">
<h1></h1>
<p> HTML  ,   {'<input>'},{'<textarea>'}  {'<select>'},       
    .  React        state 
     setState() </p>
 <div className="componentscode">
     <pre>
         <code>
         class NameForm extends React.Component {'{'} <br/>
  constructor(props) {'{'} <br/>
    super(props); <br/>
    this.state ={'{'}value: ''{'}'}; <br/>

    this.handleChange = this.handleChange.bind(this); <br/>
    this.handleSubmit = this.handleSubmit.bind(this); <br/>
  {'}'}<br/>
  handleChange(event) {'{'} <br/>
    this.setState({'{'}value: event.target.value{'}'}); <br/>
  {'}'}<br/>
  render() {'{'}<br/>
    return (<br/>
      {'<form onSubmit={this.handleSubmit}>'} <br/>
        {'<label>'} <br/>
          :
         {' <input type="text" value={this.state.value} onChange={this.handleChange} />'} <br/>
        {'</label>'} <br/>
       {'<input type="submit" value="" />'} <br/>
      {'</form>'} <br/>
    ); <br/>
  {'}'} <br/>
{'}'} <br/>
         </code>
     </pre>
 </div>
 <div>
 <Search placeholder="input search loading default" loading />
    <br />
    <br />
    <Search placeholder="input search loading with enterButton" loading enterButton />
    <br />
    <br />
    <Search placeholder="input search text" enterButton="Search" size="large" loading />
 </div>
<p>   value   ,         this.state.value.
      React-   .        handleChange,
         React-,        ,   .</p>
      </div>
  )
}

export default Form


