import React from "react";
import { Rate } from 'antd';
import { FrownOutlined, MehOutlined, SmileOutlined } from '@ant-design/icons';
const customIcons = {
  1: <FrownOutlined />,
  2: <FrownOutlined />,
  3: <MehOutlined />,
  4: <SmileOutlined />,
  5: <SmileOutlined />,
};

function Fragment(){
    return(
<div className="fragment">
    <h1></h1>
    <div>
     <Rate defaultValue={3} character={({ index }) => customIcons[index + 1]} />
    </div>
    <p>     ,      DOM</p>
    <div className="componentscode" >
        <pre>
            <code>
            render() {'{'} <br/>
  return ( <br/>
    {'<React.Fragment>'} <br/>
     {' <ChildA />'} <br/>
     {' <ChildB />'} <br/>
     {' <ChildC />'} <br/>
   {' </React.Fragment>'} <br/>
  ); <br/>
{'}'} <br/>
            </code>
        </pre>
    </div>
    <p>   .</p>
    <div className="componentscode">
        <pre>
            <code>
            class Columns extends React.Component {'{'} <br/>
  render() {'{'} <br/>
    return ( <br/>
     {'<>'} <br/>
        <td></td> <br/>
        <td></td> <br/>
     {'</>'} <br/>
    ); <br/>
  {'}'} <br/>
{'}'} <br/>
            </code>
        </pre>
    </div>
  <h2>  </h2> 
  <p>,    {'<React.Fragment>'},   . ,     
        ,     .
          `key`, React     .</p> 
 <div className="componentscode">
     <pre>
         <code>
         function Glossary(props) {'{'} <br/>
  return ( <br/>
    {'<dl>'} <br/>
      {'{'}props.items.map(item {'=>'}( <br/>
        {'<React.Fragment key={item.id}>'} <br/>
         {'<dt>{item.term}</dt>'} <br/>
         {'<dd>{item.description}</dd>'} <br/>
        {'</React.Fragment>'} <br/>
      )){'}'} <br/>
   {'</dl>'} <br/>
  ); <br/>
{'}'} <br/>
         </code>
     </pre>
 </div>
 <p>key    ,   Fragment.        , 
     ,  .</p>
</div> 
    )  
}

export default Fragment