import React from "react";
import { Collapse } from 'antd';
const { Panel } = Collapse;

const text = `
useState    (   ).   ,      
. React      .  useState     , 
:        .      , , 
 .    this.setState  ,        .   
      .
`;

const text2 = `
    useEffect        .     ,
   componentDidMount, componentDidUpdate  componentWillUnmount  React-,     API
`;
const text3 = `
   (,   React.createContext)       
 .      value  <MyContext.Provider>     
 .
`;
function Hooks(){
  
  const onChange = (key) => {
    console.log(key)
  };

  return (
      <>
    <h1 className="hook"></h1>
    <Collapse defaultActiveKey={['1']} onChange={onChange}>
      <Panel header="useState" key="1">
        <p>{text}</p>
      </Panel>
      <Panel header="useEffect" key="2">
        <p>{text2}</p>
      </Panel>
      <Panel header="useContext" key="3">
        <p>{text3}</p>
      </Panel>
    </Collapse>
    </>
  );
  
    
}

export default Hooks